@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,900i');

body {
  background-color: rgb(237, 241, 245) !important;
}

.container-login{
  width: 100%;
  min-height: 100vh;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 15px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  background: #cb2d3e;  /* fallback for old browsers */
  background-image: -webkit-linear-gradient(to left, #ef473a, #cb2d3e);  /* Chrome 10-25, Safari 5.1-6 */
  background-image: linear-gradient(to left, #ef473a, #cb2d3e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-image: url("https://images.unsplash.com/photo-1491438590914-bc09fcaaf77a?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9");
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
}

.bg-dark{
  background: #FF5F6D;
  background: -webkit-linear-gradient(to right, #FFC371, #FF5F6D);
  background: linear-gradient(to right, #FFC371, #FF5F6D);
}

.box-login{
  width: 390px;
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 3px 20px 0 rgba(0,0,0,.1);
  -moz-box-shadow: 0 3px 20px 0 rgba(0,0,0,.1);
  -webkit-box-shadow: 0 3px 20px 0 rgba(0,0,0,.1);
  -o-box-shadow: 0 3px 20px 0 rgba(0,0,0,.1);
  -ms-box-shadow: 0 3px 20px 0 rgba(0,0,0,.1);
}


.box{
  overflow: hidden;
  box-shadow: 0 3px 20px 0 rgba(0,0,0,.1);
  -moz-box-shadow: 0 3px 20px 0 rgba(0,0,0,.1);
  -webkit-box-shadow: 0 3px 20px 0 rgba(0,0,0,.1);
  -o-box-shadow: 0 3px 20px 0 rgba(0,0,0,.1);
  -ms-box-shadow: 0 3px 20px 0 rgba(0,0,0,.1);
  border: 1px solid #e2e2e2;
  border-radius: 3px;
  box-shadow: 0 0 5px 0 #e2e2e2;
  padding: 15px;
  background-color: white;
  margin-top: 15px;
}

.form-login{
  width: 100%;
}

.img-circle{
  max-height:100px; 
  margin:auto;
  position: relative;
  margin: auto;
  border-radius: 50px;
  margin-left: 25px;
}

textarea {
  width: 100%;
}

.navbar{
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}

.navbar-light .navbar-brand {
  color: white!important;
}

.navbar-brand{ 
  font-family: 'Source Sans Pro', sans-serif;
}


.form-login-title{
  font-family: 'Source Sans Pro', sans-serif;
    font-size: 30px;
    color: #2a2a2a;
    line-height: 1.2;
    text-align: center;
    padding-bottom: 5px;
}

.btn{
   font-family: 'Source Sans Pro', sans-serif;
   display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    min-width: 80px;
    height: 40px;
    border-radius: 25px;
    font-size: 14px;
    color: #fff;
    line-height: 1.2;
    text-transform: uppercase;
    -webkit-transition: all .4s;
    -o-transition: all .4s;
    -moz-transition: all .4s;
    transition: all .4s;
    background-color: #343a40;
    
}

.btn-green{
  background-color: #00ba54;
  box-shadow: 0 10px 30px 0 rgba(5, 172, 80, 0.5);
    -moz-box-shadow: 0 10px 30px 0 rgba(5, 172, 80, 0.5);
    -webkit-box-shadow: 0 10px 30px 0 rgba(5, 172, 80, 0.5);
    -o-box-shadow: 0 10px 30px 0 rgba(5, 172, 80, 0.5);
}

.btn-red{
  background-color: #eb173b;
  box-shadow: 0 10px 30px 0 rgba(235, 39, 39, 0.5);
    -moz-box-shadow: 0 10px 30px 0 rgba(235, 39, 39, 0.5);
    -webkit-box-shadow: 0 10px 30px 0 rgba(235, 39, 39, 0.5);
    -o-box-shadow: 0 10px 30px 0 rgba(235, 39, 39, 0.5);
}

.p-r-55 {
  padding-right: 55px;
}
.p-l-55 {
  padding-left: 55px;
}
.p-b-30 {
  padding-bottom: 30px;
}
.p-t-80 {
  padding-top: 80px;
}

